export const inputHandler = {
    methods : {
        getOptionSelect(item,field){
            let self = this;
            // –û–±—Ä–∞—Ç–Ω–∞—è –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç—å
            let backwardDepend = field.dependent_data && field.dependent_data.backward_dependency || false;
            // –ó–∞–≤–∏—Å–∏–º—ã–π select
            let dependent = field.dependent_data && field.dependent_data.dependent || false;

            // –ü—Ä—è–º–∞—è –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç—å
            function directDependency() {
                return self.add_models[field.options_data.options_data_model]
                    .filter(elem=>{
                        return elem[field.dependent_data.dependent_field] === item[field.dependent_data.dependent_field]
                    } )
            }

            // –û–±—Ä–∞—Ç–Ω–∞—è –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç—å
            function backwardDependency() {
                let foundItem = self.add_models[field.dependent_data.dependent_data_model].filter(e=>{
                    return e[field.dependent_data.dependent_field] === item[field.dependent_data.dependent_backward_field]
                })[0];
                if(typeof foundItem === 'undefined') return [];
                return self.add_models[field.options_data.options_data_model].filter(elem=>{
                    return elem[field.dependent_data.dependent_field] === foundItem[field.dependent_data.dependent_field_id]
                } )
            }

            // –í—ã–≤–µ—Å—Ç–∏ –≤—Å–µ –≤–∞—Ä–∏–∞–Ω—Ç—ã
            function defaultState() {
                return self.add_models[field.options_data.options_data_model]
            }

            let options = backwardDepend === true ?  backwardDependency() : !backwardDepend && dependent ? directDependency() : defaultState();

            if(options.length === 0 && this.$refs[field.key] && this.$refs[field.key][0])
                this.$refs[field.key][0]._data._value = '';

            return options;
        },
        /**
         * –í—ã–±–æ—Ä select
         * @param {Object} field
         * @param {Object} item
         */
        async chooseOption(field,item){
            if(field.dependent_data && field.dependent_data.supreme){
                this.resetDependentSelect(field,item,this.block.block_fields)
            }
            if(this.block.block_parameters.choose_first_option){
                this.chooseFirstOption(field,item);
            }
        },
        async chooseFirstOption(field,item){
            let key = field.dependent_data.supreme_field_key;
            let fieldFind = this.block.block_fields.filter(e=>e.key === key)[0];
            let title = fieldFind.options_data.options_field_title;
            let options = await this.getOptionSelect(item,fieldFind)
            options[0] ? item[key] = options[0].id : false;
            this.$refs[key] && this.$refs[key][0] ? this.$refs[key][0]._data._value = options[0][title] : false;

        },
        /**
         * –û—á–∏—Å—Ç–∫–∞ –∑–∞–≤–∏—Å–∏–º–æ–≥–æ select
         * @param {Object} field
         * @param {Object} item
         * @param {array} fields
         */
            resetDependentSelect(field,item,fields){
            if(field.dependent_data && field.dependent_data.supreme){
                let supremeKey = field.dependent_data.supreme_field_key;
                item[supremeKey] = null;
                this.$refs[supremeKey] ? this.$refs[supremeKey][0]._data._value = '' : false;

                fields.forEach(e=>e.key === supremeKey ? this.resetDependentSelect(e,item,fields) : false)
            }
        },
    }
}
